.nav {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 20px;
  height: 30px;
  z-index: 1;
  transition-timing-function: ease-in;
  transition: all 0.5s;
}

.nav__logo {
  width: 80px;
  position: fixed;
  left: 20px;
  object-fit: contain;
  padding: 15px;
}
.nav__avatar {
  width: 30px;
  object-fit: contain;
  position: fixed;
  right: 20px;
  padding: 15px;
}
.nav__black {
  background-color: #111;
}
.logo{
  height:50px; 
   background-color: black; 
